module.exports = " <header class=template-header> <span class=border-left-orange>{{title}}</span> <button class=\"btn btn-default\" v-if=isEdit @click=back()><i class=\"fa fa-reply\"></i> 返回推荐列表</button> </header> <div class=content-body> <panel title=Banner类型> <div slot=panel-body class=panel-body> <ul class=\"nav nav-pills\" v-if=!isEdit> <li :class=\"step == 1? 'active': ''\"><a @click=stepSwitch(1)>商品</a></li> <li :class=\"step == 2? 'active': ''\"><a @click=stepSwitch(2)>活动</a></li> </ul> <ul class=\"nav nav-pills\" v-else> <li class=active v-if=\"step == 1\"><a>商品</a></li> <li class=active v-if=\"step == 2\"><a>活动</a></li> </ul> </div> </panel> <panel :title=\"step == 1? '选择商品' :'选择活动'\"> <div slot=panel-body class=panel-body> <ul class=\"nav nav-pills\" v-if=!commend.item_id> <li><a @click=openModal()>{{buttonName[step - 1]}}</a></li> </ul> <div v-if=commend.item_id> <span>已选活动：{{commend.item_name}}</span> <a class=margin-left-10 @click=openModal()>修改</a> </div> </div> </panel> <panel title=展示信息> <div slot=panel-body class=panel-body> <form class=form-horizontal role=form> <div class=form-group> <label class=\"control-label col-sm-2\"> <span>主标题</span> </label> <div class=\"col-sm-6 col-lg-4\"> <input class=\"form-control input-sm\" type=text placeholder=请输入主标题 v-model=commend.name> </div> </div> <div class=form-group> <label class=\"control-label col-sm-2\"> <span>副标题</span> </label> <div class=\"col-sm-6 col-lg-4\"> <input type=text class=\"form-control input-sm\" placeholder=请输入副标题 v-model=commend.sub_name> </div> </div> <div class=form-group> <label class=\"control-label col-sm-2\"> <span>角标</span> </label> <div class=\"col-sm-6 col-lg-4\"> <input type=text class=\"form-control input-sm\" placeholder=请输入角标，2-4字 v-model=commend.script> </div> </div> <div class=form-group> <label class=\"control-label col-sm-2\"> <span>主题图</span> </label> <div class=\"col-sm-6 padding-left-6\"> <upload-img :img-src.sync=commend.pic file-id=img1 upload-id=upload-1 width=400px height=200px></upload-img> </div> <div class=\"col-sm-6 col-sm-offset-2 margin-top-10\"> <p class=\"text-red content-img\">图片用作首页推荐位置显示，请使用 300x200 尺寸的图片，支持png/jpg格式</p> </div> </div> </form> </div> </panel> <button id=btn class=\"btn button-primary\">{{saveButton}}</button> <modal :show.sync=choiceModal :title=\"buttonName[step - 1]\" :header=false :footer=false :width=1000> <div slot=modal-body class=modal-body> <form class=form-horizontal role=form> <div class=form-group> <div class=col-sm-2 v-if=\"step == 1\"> <select class=\"form-control input-sm\" v-model=choice.type> <option value={{$index}} v-for=\"type in productType\">{{type}}</option> </select> </div> <div class=col-sm-2 v-if=\"step == 2\"> <select class=\"form-control input-sm\" v-model=choice.type> <option value={{$index}} v-for=\"type in activityType\">{{type}}</option> </select> </div> <div class=col-sm-6> <input type=text class=\"form-control input-sm\" placeholder=请输入关键字搜索 v-model=choice.name> </div> <div class=col-sm-1> <a><i class=\"fa fa-search\" @click=getList()></i></a> </div> <div class=col-sm-2> <a><i class=\"fa fa-angle-left\" @click=turnPage(0)></i></a> <a><i class=\"fa fa-angle-right\" @click=turnPage(1)></i></a> </div> <div class=col-sm-1> <a @click=close><i class=\"fa fa-times\"></i></a> </div> </div> <div class=\"form-group border-black-1\" style=\"margin: 10px 0px\" v-for=\"item in list\"> <div class=col-sm-3> <img :src=item.pic_thumb alt=image class=item-image> </div> <div class=col-sm-5> <h4 class=margin-top-30>{{item.name}}</h4> </div> <div class=col-sm-2> <h4 class=margin-top-30>{{item.type_str}}</h4> </div> <div class=\"col-sm-2 text-right\"> <button class=\"btn button-primary margin-top-20\" @click.prevent=choiceSave(item)>选择</button> </div> </div> </form> </div> </modal> </div> ";


