<style lang="sass">
.switch {
	position: relative;
	width: 36px;
	height: 20px;
}

/* Hide default HTML checkbox */
.switch input {display:none;}

/* The slider */
.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #ccc;
	-webkit-transition: .4s;
	transition: .4s;
}

.slider:before {
	position: absolute;
	content: "";
	height: 16px;
	width: 16px;
	left: 2px;
	bottom: 2px;
	background-color: white;
	-webkit-transition: .4s;
	transition: .4s;
}

input:checked + .slider {
	background-color: #2196F3;
}

input:focus + .slider {
	box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
	-webkit-transform: translateX(16px);
	-ms-transform: translateX(16px);
	transform: translateX(16px);
}

/* Rounded sliders */
.slider.round {
	border-radius: 16px;
}

.slider.round:before {
	border-radius: 50%;
}
</style>
<template lang="html">
<div class="switch">
	<slot name="switch">
        <label class="switch">
		  	<input type="checkbox" @click="callback" checked="{{exchangeLimit}}">
		  	<div class="slider round"></div>
		</label>
    </slot>
</div>
</template>
<script>
export default {
	props: {
		callback: {
			type: Function,
        	default() {}
		},
		exchangeLimit: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
		}
	},
	methods: {
	}
}

</script>


