module.exports = " <header class=template-header> <span class=border-left-orange>订单详情</span> <button class=\"btn btn-default\" @click.prevent=back()><i class=\"fa fa-reply\"></i> 返回订单列表</button> </header> <div class=content-body> <panel title=基本信息> <div slot=panel-body class=panel-body> <ul class=nav> <li>订单号：{{detail.orderid}}</li> <li>用户昵称：{{detail.nickname}}</li> <li>订单类型：{{orderType}}</li> <li>订单内容：{{detail.product}}</li> <li>订单来源：{{detail.origin_type_str}}</li> <li>消耗积分：{{detail.integral}}</li> </ul> </div> </panel> <panel title=订单流程> <div slot=panel-body class=\"panel-body order-timeline\"> <div v-if=\"detail.send_type == 1\"> <ul class=timeline v-if=\"detail.product_type == 2\"> <li class=timeline v-for=\"item in traceList\"> <div class=timeline-time>{{item.AcceptTime}}</div> <div class=timeline-node></div> <div class=timeline-content>{{item.AcceptStation}}</div> </li> <li v-if=detail.express_time> <div class=timeline-time>{{detail.express_time}}</div> <div class=timeline-node></div> <div class=timeline-content>已发货，运单号：{{detail.express_id}}</div> </li> <li v-if=detail.address_time> <div class=timeline-time>{{detail.address_time}}</div> <div class=timeline-node></div> <div class=timeline-content>确认收货地址：{{detail.address}}</div> </li> <li v-else> <div class=timeline-time>{{detail.create_time}}</div> <div class=timeline-node></div> <div class=timeline-content>等待用户确认收货地址</div> </li> <li> <div class=timeline-time>{{detail.create_time}}</div> <div class=timeline-node></div> <div class=timeline-content>创建订单，消耗{{detail.integral}}积分</div> </li> </ul> <ul class=timeline v-else> <li> <div class=timeline-time>{{detail.create_time}}</div> <div class=timeline-node></div> <div class=timeline-content v-if=\"detail.type == 1\">券码已发放。</div> <div class=timeline-content v-else>获得奖品：{{detail.product}}</div> </li> <li> <div class=timeline-time>{{detail.create_time}}</div> <div class=timeline-node></div> <div class=timeline-content>创建订单，消耗{{detail.integral}}积分</div> </li> </ul> </div> <div v-else> <ul class=timeline> <li v-if=\"detail.status == 4\"> <div class=timeline-time>{{detail.close_time}}</div> <div class=timeline-node></div> <div class=timeline-content>订单已逾期</div> </li> <li v-if=\"detail.status == 3\"> <div class=timeline-time>{{detail.take_time}}</div> <div class=timeline-node></div> <div class=timeline-content>客户已取货</div> </li> <li> <div class=timeline-time>{{detail.create_time}}</div> <div class=timeline-node></div> <div class=timeline-content>等待客户取货</div> </li> <li> <div class=timeline-time>{{detail.create_time}}</div> <div class=timeline-node></div> <div class=timeline-content>创建订单，消耗{{detail.integral}}积分</div> </li> </ul> </div> </div> </panel> </div> ";


